:root {
  --font-family: "Roboto", sans-serif;
  --main-bg: white;
}

body {
  font-family: var(--font-family);
  background-color: var(--main-bg);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

nav {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  min-width: 320px;
  max-width: 1158px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.navList {
  flex-direction: row;
  gap: 35px;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 36px;
  display: flex;
}

.navList a {
  transition: background-color .3s cubic-bezier(.165, .84, .44, 1);
}

.navList a:hover, .navList a:focus, .currentPage {
  color: #00f;
}

main {
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.shopList {
  min-width: 200px;
  height: 500px;
  border: 3px solid gray;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 30px 50px;
  font-size: 25px;
  display: flex;
}

.itemShoplist {
  border: 3px solid gray;
  border-radius: 12px;
  padding: 10px 30px;
  transition: background-color .3s cubic-bezier(.165, .84, .44, 1);
}

.itemShoplist:hover, .itemShoplist:focus {
  background-color: gray;
}

.drugsList {
  height: 500px;
  min-width: 700px;
  border: 3px solid gray;
  border-radius: 12px;
  flex-wrap: wrap;
  gap: 24px;
  padding: 30px 50px;
  display: flex;
  overflow-y: scroll;
}

.itemDrugs {
  box-sizing: border-box;
  border: 3px solid gray;
  border-radius: 12px;
  flex: calc(50% - 12px);
  padding: 12px;
}

button {
  cursor: pointer;
  border-radius: 12px;
  padding: 8px;
  font-size: 24px;
  transition: background-color .3s cubic-bezier(.165, .84, .44, 1);
}

button:hover, button:focus {
  background-color: gray;
}

.drugsBasket {
  height: 500px;
  min-width: 700px;
  border: 3px solid gray;
  border-radius: 12px;
  padding: 30px 50px;
  overflow-y: scroll;
}

.itemDrugsBasket {
  box-sizing: border-box;
  border: 3px solid gray;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 12px;
  display: flex;
  position: relative;
}

.btnDelete {
  position: absolute;
  top: 10px;
  right: 20px;
}

.infoDrug {
  max-width: 220px;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  font-size: 20px;
  display: flex;
}

.inputSection {
  height: 500px;
  min-width: 200px;
  border: 3px solid gray;
  border-radius: 12px;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 50px;
  display: flex;
}

.inputSection form {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.contact-form {
  border-radius: 5px;
  flex-direction: column;
  align-items: flex-end;
  gap: 24px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.order-form {
  flex-direction: row;
  gap: 24px;
  display: flex;
}

.contact-form label {
  margin-bottom: 10px;
  display: block;
}

.contact-form input[type="text"], .contact-form input[type="email"], .contact-form input[type="tel"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 15px;
  padding: 8px;
}

.submit-info {
  align-items: center;
  gap: 30px;
  font-size: 24px;
  display: flex;
}

